.bsa-menu li a {
  position: relative;
  padding-left: 16px;
  padding-right: 24px;
  color: var(--bs-body-color);
  text-decoration: none;
  height: 40px;
  line-height: 40px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
